        methods: {
            getColorByClasses(classes) {
                switch (classes) {
                    case "DK":
                        return "rgb(77%,12%,23%)";
                    case "XD":
                        return "rgb(100%,49%,4%)";
                    case "LR":
                        return "rgb(67%,83%,45%)";
                    case "FS":
                        return "rgb(41%,80%,94%)";
                    case "WS":
                        return "#2D9B78";
                    case "QS":
                        return "rgb(96%,55%,73%)";
                    case "MS":
                        return "rgb(100%,100%,100%)";
                    case "DZ":
                        return "rgb(100%,96%,41%)";
                    case "SM":
                        return "rgb(14%,35%,100%)";
                    case "SS":
                        return "rgb(58%,51%,79%)";
                    case "ZS":
                        return "rgb(78%,61%,43%)";
                    case "DH":
                        return "#a330c9";
                }
            },
            getSort() {
                for (let i = 0; i < this.users.length; i++) {
                    this.users[i].maxScore = 0;
                    for (let j = 0; j < this.users[i].steps.length; j++) {
                        if (this.users[i].steps[j].level > this.users[i].maxScore) {
                            this.users[i].maxScore = this.users[i].steps[j].level;
                        }
                    }
                }
                return this.users.sort(function (a, b) {
                    return a.maxScore < b.maxScore ? 1 : -1
                });
            }
        },
        mounted() {
            this.users = this.getSort();
        }
    }
